import variables from "./variables";
import { onRightArrowPress, onLeftArrowPress } from "./leftRightArrowPress";

// Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¿Ð¾ Ð­ÑÐº.
export function onEscapeKeyPress(evt) {
  if (evt.code === "Escape") {
    onCloseModal();
  }
}

//Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¿Ð¾ ÐºÐ½Ð¾Ð¿ÐºÐµ
export function onModalBtnClosePress() {
  if (variables.modalCloseBtnEl.nodeName === "BUTTON") {
    onCloseModal();
  }
}

//Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑƒ Ð½Ð° Ð±ÑÐºÐ´Ñ€Ð¾Ð¿
export function onBackdropClickClose(evt) {
  if (evt.currentTarget === evt.target) {
    onCloseModal();
  }
}

//ÑÐ°Ð¼Ð¾ Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¸ ÑÐ½ÑÑ‚Ð¸Ðµ ÑÐ»ÑƒÑˆÐ°Ñ‚ÐµÐ»ÐµÐ¹ ÐºÐ»Ð°Ð²Ð¸Ð°Ñ‚ÑƒÑ€Ñ‹. Ð£Ð´Ð°Ð»ÑÐµÑ‚ src
export function onCloseModal() {
  variables.backdropOverlayContainer.classList.remove("is-open");

  window.removeEventListener("keydown", onEscapeKeyPress);
  window.removeEventListener("keydown", onRightArrowPress);
  window.removeEventListener("keydown", onLeftArrowPress);

  deleteImgSrc();
}

//ÑÐ°Ð¼Ð¾ ÑƒÐ´Ð°Ð»ÐµÐ½Ð¸Ðµ src
function deleteImgSrc() {
  variables.modalImgEl.src = "";
}

// ÑÐºÑÐ¿Ð¾Ñ€Ñ‚ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¹
export default {
  onEscapeKeyPress,
  onModalBtnClosePress,
  onBackdropClickClose,
  onCloseModal,
  deleteImgSrc,
};
